/**
 * @file ERA  - 
 * @description
 *  `<VariableInsert>`  `EditLog`
 *
 * ****:
 * - ****: 
 * - ****: `applyInsertAtLevel` 
 *   
 * - ****: 
 *   
 */
'use strict';

import { sanitizeArrays } from '../../../utils/data';
import { Logger } from '../../../utils/log';
import { applyTemplateToPatch, getInheritedTemplateContent, resolveTemplate } from './template';

const logger = new Logger();

/**
 * ****
 *  `<VariableInsert>` ****
 *
 * ****:
 * 1. ****
 * 2. **** `player.inventory` `inventory`
 *     `insert` 
 * 3. ****
 * 4. **** `$template` 
 *
 * @param {any} statData -  ( `stat_data`)
 * @param {string} basePath - 
 * @param {any} patchObj - 
 * @param {any[]} editLog - 
 * @param {any} inheritedContent - 
 * @param {any} parentData -  `statData` 
 */
export function applyInsertAtLevel(
  statData: any,
  basePath: string,
  patchObj: any,
  editLog: any[],
  inheritedContent: any,
  parentData: any, // 
) {
  // --- 1.  ---
  //  resolveTemplate parentData
  const localTplContent = resolveTemplate(inheritedContent, parentData);
  logger.debug('applyInsertAtLevel', `[] basePath: "${basePath || 'root'}"`, {
    statData: _.cloneDeep(statData),
  });

  // --- 2.  ---
  const currentNodeInVars = basePath ? _.get(statData, basePath) : statData;
  const unInsertAble = _.get(currentNodeInVars, ['$meta', 'unInsertAble']);
  logger.debug(
    'applyInsertAtLevel',
    `[] at path: "${basePath || 'root'}". currentNodeInVars :`,
    currentNodeInVars,
  );

  // ** (Atomic Insert)**
  // 
  if (basePath && currentNodeInVars === undefined) {
    //  applyTemplateToPatch 
    const composed = applyTemplateToPatch(localTplContent, patchObj);

    const finalValue = sanitizeArrays(composed); //  null 
    logger.debug('applyInsertAtLevel', ` at ${basePath}:\n${JSON.stringify(finalValue, null, 2)}`);
    _.set(statData, basePath, finalValue); // 
    editLog.push({ op: 'insert', path: basePath, value_new: _.cloneDeep(finalValue) });
    logger.debug('applyInsertAtLevel', `: ${basePath}`);
    return; // 
  }

  // ** (Recursive Supplement)**
  // 
  if (_.isPlainObject(currentNodeInVars) && _.isPlainObject(patchObj)) {
    const patchKeys = Object.keys(patchObj);
    const isMetaOnlyPatch = patchKeys.every(key => key === '$meta');
    const hasMeaningfulPatch = patchKeys.length > 0;

    if (unInsertAble === 'all' && hasMeaningfulPatch && !isMetaOnlyPatch) {
      logger.warn(
        'applyInsertAtLevel',
        `VariableInsert  <${basePath || 'root'}>  "$meta.unInsertAble: all" `,
        { basePath, patchObj, currentNodeInVars },
      );
      return;
    }
    logger.debug(
      'applyInsertAtLevel',
      `[] at path: "${basePath || 'root'}"
      -  (localTplContent): ${JSON.stringify(localTplContent)}`,
    );
    for (const key of patchKeys) {
      if (unInsertAble === 'self' && key !== '$meta' && !_.has(currentNodeInVars, key)) {
        logger.warn(
          'applyInsertAtLevel',
          `VariableInsert  <${basePath || 'root'}>  "$meta.unInsertAble: self"  "${key}"`,
          { basePath, patchObj, currentNodeInVars, rejectedKey: key },
        );
        continue;
      }
      const subPath = basePath ? `${basePath}.${key}` : key;
      const subPatch = patchObj[key];
      //  getInheritedTemplateContent
      const subInheritedContent = getInheritedTemplateContent(localTplContent, key);
      logger.debug(
        'applyInsertAtLevel',
        `  - : "${key}"
      -  (subInheritedContent): ${JSON.stringify(subInheritedContent)}`,
      );
      //  currentNodeInVars  parentData 
      applyInsertAtLevel(statData, subPath, subPatch, editLog, subInheritedContent, currentNodeInVars);
    }
  } else if (basePath) {
    // ****
    // 
    // insert 
    logger.warn('applyInsertAtLevel', `VariableInsert  -> ${basePath}`, {
      basePath,
      patchObj,
      currentNodeInVars,
    });
  }
  //  basePath  patch 
}

/**
 * ****  `<VariableInsert>` 
 *
 * @param {any[]} allInserts -  insert 
 * @param {any} initialStat -  `stat_data` 
 * @returns {Promise<{ finalStat: any, editLog: any[] }>} - 
 */
export async function processInsertBlocks(
  allInserts: any[],
  initialStat: any,
): Promise<{ finalStat: any; editLog: any[] }> {
  if (!allInserts || allInserts.length === 0) {
    return { finalStat: initialStat, editLog: [] };
  }

  logger.debug('processInsertBlocks', '[]  processInsertBlocks  statData:', _.cloneDeep(initialStat));

  const currentStat = _.cloneDeep(initialStat);
  const editLog: any[] = [];

  /*
   *  insertRoot
   *  insertRoot  insertRoot  `currentStat` 
   *  <VariableInsert> 
   *  <VariableInsert> 
   */
  for (const insertRoot of allInserts) {
    if (!_.isPlainObject(insertRoot) || _.isEmpty(insertRoot)) continue;
    try {
      logger.debug('processInsertBlocks', ` insertRoot: ${JSON.stringify(insertRoot)}`);
      //  ''  null
      applyInsertAtLevel(currentStat, '', insertRoot, editLog, null, null);
    } catch (e: any) {
      logger.error('processInsertBlocks', ` insertRoot : ${e?.message || e}`, {
        error: e,
        insertRoot,
        currentStat,
      });
    }
  }

  logger.debug('processInsertBlocks', ' VariableInsert ');
  return { finalStat: currentStat, editLog };
}
