'use strict';

import { ERA_DATA_REGEX } from './constants';
import { escapeEraData, parseJsonl } from './data';
import { Logger } from './log';
import { containsXMLTags, createTagRegex, removeTagsByRegex, stripCodeFence, traditionalToSimplified } from './string';
import { parseCharacterMacros } from './text';

const log = new Logger();

export function extractMessageIdFromDetail(detail: any): number | null {
  if (detail === null || detail === undefined) {
    log.debug('extractMessageIdFromDetail', 'detail  message_id');
    return null;
  }

  if (typeof detail === 'number') {
    if (Number.isFinite(detail)) {
      log.debug('extractMessageIdFromDetail', ' number detail  message_id', { message_id: detail });
      return detail;
    }
    log.debug('extractMessageIdFromDetail', 'number detail ', { detail });
    return null;
  }

  if (typeof detail === 'string') {
    const parsed = Number(detail);
    if (Number.isFinite(parsed)) {
      log.debug('extractMessageIdFromDetail', ' string detail  message_id', {
        raw: detail,
        message_id: parsed,
      });
      return parsed;
    }
    log.debug('extractMessageIdFromDetail', 'string detail ', { raw: detail });
    return null;
  }

  if (Array.isArray(detail) && detail.length > 0) {
    const parsed = Number(detail[0]);
    if (Number.isFinite(parsed)) {
      log.debug('extractMessageIdFromDetail', ' detail[0]  message_id', {
        raw: detail[0],
        message_id: parsed,
      });
      return parsed;
    }
    log.debug('extractMessageIdFromDetail', ' detail[0] ', { raw: detail[0] });
    return null;
  }

  if (typeof detail === 'object') {
    if (typeof detail.message_id === 'number') {
      if (Number.isFinite(detail.message_id)) {
        log.debug('extractMessageIdFromDetail', ' detail.message_id(number)  message_id', {
          message_id: detail.message_id,
        });
        return detail.message_id;
      }
      log.debug('extractMessageIdFromDetail', ' detail.message_id(number) ', {
        message_id: detail.message_id,
      });
      return null;
    }
    if (typeof detail.message_id === 'string') {
      const parsed = Number(detail.message_id);
      if (Number.isFinite(parsed)) {
        log.debug('extractMessageIdFromDetail', ' detail.message_id(string)  message_id', {
          raw: detail.message_id,
          message_id: parsed,
        });
        return parsed;
      }
      log.debug('extractMessageIdFromDetail', ' detail.message_id(string) ', {
        raw: detail.message_id,
      });
      return null;
    }
    if (Array.isArray(detail.eventArgs) && detail.eventArgs.length > 0) {
      const parsed = Number(detail.eventArgs[0]);
      if (Number.isFinite(parsed)) {
        log.debug('extractMessageIdFromDetail', ' detail.eventArgs[0]  message_id', {
          raw: detail.eventArgs[0],
          message_id: parsed,
        });
        return parsed;
      }
      log.debug('extractMessageIdFromDetail', ' detail.eventArgs[0] ', {
        raw: detail.eventArgs[0],
      });
      return null;
    }
  }

  log.debug('extractMessageIdFromDetail', ' detail  message_id', { detail });
  return null;
}

/**
 * @type {EraData} -  `<era_data>` 
 */
type EraData = {
  'era-message-key': string;
  'era-message-type': 'user' | 'assistant';
};

// ==================================================================
// 
// ==================================================================

/**
 * **** 
 *  ERA 
 * @param {TavernMessage} msg - 
 * @returns {string | null}  null
 */
export function getMessageContent(msg: any): string | null {
  if (!msg) return null;

  let content: string | null = null;

  //  .mes 
  if (typeof msg.mes === 'string') {
    content = msg.mes;
  }
  //  .mes swipes
  else if (Array.isArray(msg.swipes)) {
    const sid = Number(msg.swipe_id ?? 0);
    content = msg.swipes[sid] || null;
  }
  //  .message 
  else if (typeof msg.message === 'string') {
    content = msg.message;
  }

  if (content === null) {
    return null;
  }

  // 
  // 
  // 
  // 
  return parseCharacterMacros(content);
}

/**
 *  `EraData` 
 * @param {string | null | undefined} messageContent - 
 * @returns {EraData | null}  `EraData`  null
 */
function parseEraData(messageContent: string | null | undefined): EraData | null {
  if (typeof messageContent !== 'string') {
    return null;
  }
  const match = messageContent.match(ERA_DATA_REGEX);
  if (!match || !match[1]) {
    return null;
  }
  try {
    //  MK  `{"key"="value"}`  JSON.parse
    // 
    const customFormatBlock = match[1];
    const keyMatch = customFormatBlock.match(/"era-message-key"\s*=\s*"(.*?)"/);
    const typeMatch = customFormatBlock.match(/"era-message-type"\s*=\s*"(.*?)"/);

    if (keyMatch?.[1] && typeMatch?.[1]) {
      const eraData = {
        'era-message-key': keyMatch[1],
        'era-message-type': typeMatch[1] as 'user' | 'assistant',
      };
      log.debug('parseEraData', ' EraData', eraData);
      return eraData;
    }
    log.debug('parseEraData', ' EraData ', { customFormatBlock });
    return null;
  } catch (e) {
    log.warn('parseEraData', ' EraData ', e);
    return null;
  }
}

/**
 * ****  `era-message-type`  `role` 
 * 
 * @param {TavernMessage} msg - 
 * @returns {boolean}  true
 */
export function isUserMessage(msg: any): boolean {
  const content = getMessageContent(msg);
  const data = parseEraData(content);
  if (data) {
    return data['era-message-type'] === 'user';
  }
  //  role 
  return msg?.role === 'user';
}

/**
 *  AI 
 * @returns {TavernMessage | null}  AI  null
 */
export function findLastAiMessage(): any | null {
  const messages = getChatMessages('0-{{lastMessageId}}', { include_swipes: true });
  if (!messages || messages.length === 0) {
    log.debug('findLastAiMessage', ', ');
    return null;
  }

  for (let i = messages.length - 1; i >= 0; i--) {
    const msg = messages[i];
    if (!isUserMessage(msg)) {
      log.debug('findLastAiMessage', ` AI , ID: ${msg.message_id}`);
      return msg;
    }
  }

  log.debug('findLastAiMessage', ' AI ');
  return null;
}

/**
 * ****
 * AI
 * 
 * @param {TavernMessage | number} [messageOrId] -  ID AI 
 * @param {number} [retries=3] - 
 * @param {number} [delay=50] - 
 * @returns {Promise<string | null>}  null
 */
export async function getMessageContentWithRetry(
  messageOrId?: any | number,
  retries: number = 20,
  delay: number = 50,
): Promise<string | null> {
  let msg: any;

  if (messageOrId) {
    if (typeof messageOrId === 'number') {
      const messages = getChatMessages(messageOrId, { include_swipes: true });
      msg = messages ? messages[0] : null;
    } else {
      msg = messageOrId;
    }
  } else {
    msg = findLastAiMessage();
  }

  if (!msg) {
    log.warn('getMessageContentWithRetry', '', { messageOrId });
    return null;
  }

  for (let i = 0; i < retries; i++) {
    const content = getMessageContent(msg);
    if (content) {
      return content;
    }
    log.log('getMessageContentWithRetry', ` ${delay}ms  ( ${i + 1} )`);
    await new Promise(resolve => setTimeout(resolve, delay));
  }

  log.warn('getMessageContentWithRetry', ` ${retries} `, { message_id: msg.message_id });
  return getMessageContent(msg); //  ( null)
}

