/**
 * **** 
 *  `swipes`  `message` 
 * @param {any} message - 
 * @param {string} newContent - 
 * @param {'none' | 'affected' | 'all'} [refresh='none'] - 
 */
export async function updateMessageContent(
  message: any,
  newContent: string,
  refresh: 'none' | 'affected' | 'all' = 'none',
) {
  const oldContent = getMessageContent(message);
  //log.debug('updateMessageContent', ':', oldContent);
  //log.debug('updateMessageContent', ':', newContent);

  const updatePayload: { message_id: number; message?: string; swipes?: string[] } = {
    message_id: message.message_id,
  };

  if (Array.isArray(message.swipes)) {
    const sid = Number(message.swipe_id ?? 0);
    const newSwipes = [...message.swipes];
    newSwipes[sid] = newContent;
    updatePayload.swipes = newSwipes;
  } else {
    updatePayload.message = newContent;
  }

  await setChatMessages([updatePayload], { refresh });
}

/**
 *  ERA 
 */
export interface EraCommand {
  type: 'insert' | 'edit' | 'delete';
  data: any[];
}

/**
 *  ERA 
 *
 * @param {any} msg - 
 * @param {boolean} [toSimplified=false] - 
 * @returns {EraCommand[]} - 
 */
export function extractAndParseCommands(msg: any, toSimplified: boolean = false): EraCommand[] {
  const rawContent = getMessageContent(msg) || '';
  if (!rawContent) return [];

  const commands: (EraCommand & { index: number })[] = [];

  // 
  // 
  const commandRegex =
    /<(Variable(Insert|Edit|Delete))>\s*(?=[\s\S]*?\S[\s\S]*?<\/\1>)((?:(?!<(?:era_data|Variable(?:Think|Insert|Edit|Delete))>|<\/\1>)[\s\S])*?)\s*<\/\1>/gi;

  let match;
  while ((match = commandRegex.exec(rawContent)) !== null) {
    const fullTagName = match[1]; // e.g., "VariableInsert"
    const shortTagName = match[2]; // e.g., "Insert"
    let blockContent = match[3]; // The content inside the tags

    // 1. 
    const cleanedBlock = stripCodeFence(blockContent.trim());
    if (!cleanedBlock) {
      continue;
    }

    // 2. () 
    const contentToParse = toSimplified ? traditionalToSimplified(cleanedBlock) : cleanedBlock;

    // 3.  JSONL JSON
    let parsedData = parseJsonl(contentToParse);
    if (parsedData.length === 0) {
      try {
        const singleJson = JSON.parse(contentToParse);
        parsedData = Array.isArray(singleJson) ? singleJson : [singleJson];
      } catch (e) {
        log.debug('extractAndParseCommands', ' JSONL  JSON', {
          content: contentToParse,
        });
        continue;
      }
    }

    if (parsedData.length === 0) {
      continue;
    }

    // 4. 
    const escapedData = escapeEraData(parsedData);

    // 5. 
    let type: EraCommand['type'];
    switch (shortTagName.toLowerCase()) {
      case 'insert':
        type = 'insert';
        break;
      case 'edit':
        type = 'edit';
        break;
      case 'delete':
        type = 'delete';
        break;
      default:
        continue;
    }

    // 6. 
    commands.push({
      type,
      data: escapedData,
      index: match.index,
    });
  }

  // 
  commands.sort((a, b) => a.index - b.index);

  if (commands.length === 0) {
    log.debug('extractAndParseCommands', ` (ID: ${msg.message_id}) `);
  } else {
    log.debug('extractAndParseCommands', '', commands);
  }

  //  index 
  return commands.map(({ index, ...rest }) => rest);
}
