import { createPinia } from 'pinia';
import { createApp, App as VueApp } from 'vue';
import { settings } from '../utils/era_data';
import { Logger } from '../utils/log';
import App from './App.vue';
import { useUiStore } from './store';
import { createMountPoint, destroyMountPoint, deteleportStyle, teleportStyle } from './utils/dom';

const logger = new Logger();
let vueApp: VueApp | null = null;
let mountPoint: JQuery<HTMLDivElement> | null = null;

/**
 * 
 * @param viewName 
 */
function switchView(viewName: 'FloatingBall' | 'ExpandedView') {
  logger.debug('switchView', `: ${viewName}`);
  // store 
  if ((window as any).eraUiStore) {
    (window as any).eraUiStore.switchView(viewName);
  } else {
    logger.warn('switchView', 'UI store');
  }
}

//  window
(window as any).eraUiSwitchView = switchView;

function unmountVueApp() {
  if (vueApp) {
    logger.debug('unmountVueApp', ' Vue ');
    vueApp.unmount();
    vueApp = null;
  }
}

function unloadUI() {
  logger.log('unloadUI', 'UI ');
  unmountVueApp();
  deteleportStyle();
  if (mountPoint) {
    logger.debug('unloadUI', '');
    destroyMountPoint();
    mountPoint = null;
  }
  // 
  window.removeEventListener('pagehide', unloadUI);
  logger.log('unloadUI', 'UI ');
}

// 
$(() => {
  if (settings.value[''] === false) {
    logger.log('initialize', 'UI...');
    unloadUI();
    return;
  }
  logger.log('initialize', 'UI ');
  // 
  mountPoint = createMountPoint();
  logger.debug('$(document).ready', '', mountPoint);

  //  body
  $('body').append(mountPoint);
  logger.debug('$(document).ready', ' body');

  //  Vue 
  vueApp = createApp(App);
  const pinia = createPinia();
  vueApp.use(pinia);
  vueApp.mount(mountPoint[0]);

  //  store  window
  const uiStore = useUiStore(pinia);
  (window as any).eraUiStore = uiStore;

  // 
  teleportStyle();
  logger.debug('initialize', 'Vue App ');

  //  era:writeDone 
  eventOn('era:writeDone', (data: any) => {
    logger.log('era:writeDone', ' era:writeDone  store', data);
    uiStore.setEventData(data); //  store action 
  });
  logger.debug('$(document).ready', ' era:writeDone ');

  // 
  window.removeEventListener('pagehide', unloadUI); // 
  window.addEventListener('pagehide', unloadUI); // 
});
