'use strict';

import { EVENT_GROUPS } from './events/merger';
import { pushToQueue } from './events/queue';

// ,  {{ERA:...}} 
import './macro/parser';
import './macro/placeholder';

//  UI 
import './ui';

// ===============================
// 
// ===============================

/**
 * @const {string[]} eventsToListen
 * @description  ERA 
 */
const eventsToListen = [
  ...EVENT_GROUPS.INIT,
  ...EVENT_GROUPS.SYNC,
  ...EVENT_GROUPS.API,
  ...EVENT_GROUPS.UPDATE_MK_ONLY,
  ...EVENT_GROUPS.COLLISION_DETECTORS,
  ...EVENT_GROUPS.COMBO_STARTERS,
  ...EVENT_GROUPS.DIRECTED_SYNC,
];

// 
// 
eventsToListen.forEach(ev => {
  // `eventOn` 
  //  `ev`  `ev`  `detail` 
  eventOn(ev, (detail: any) => pushToQueue(ev, detail));
});

// 
// 
eventOn(getButtonEvent(''), () => pushToQueue('manual_write'));
eventOn(getButtonEvent(''), () => pushToQueue('manual_sync'));
eventOn(getButtonEvent(''), () => pushToQueue('manual_full_sync'));
