//     );
//     mkToIgnore.ignoreCount--;
//     if (mkToIgnore.ignoreCount <= 0) {
//       mkToIgnore = null; // 
//       logger.debug('handleRedundantRenderEvent', ``);
//     }
//     return { shouldSkip: true, newIgnoreRule: mkToIgnore };
//   }
//   return { shouldSkip: false, newIgnoreRule: mkToIgnore };
