/**
 * ****
 * @returns {number} - 
 */
function updateConsecutiveMkCount(): number {
  const mk = logContext.mk;
  if (mk && consecutiveMkState && consecutiveMkState.mk === mk) {
    logger.debug(
      'updateConsecutiveMkCount',
      `/ MK: ${mk}: ${consecutiveMkState.count}: ${consecutiveMkState.count + 1}`,
    );
    consecutiveMkState.count++;
  } else {
    logger.debug(
      'updateConsecutiveMkCount',
      `/ MK: ${mk} 1 MK : ${consecutiveMkState?.mk}`,
    );
    consecutiveMkState = { mk: mk, count: 1 };
  }
  return consecutiveMkState.count;
}

/**
 * ****
 * 
 * @param {EventJob} job - 
 * @param {any} _mkToIgnore - () 
 * @returns {Promise<any>} - () 
 */
export async function dispatchAndExecuteTask(job: EventJob, _mkToIgnore: any): Promise<any> {
  const { type: eventType } = job;
  const eventGroup = getEventGroup(eventType);
  let message_id: number | null = null;
  const eventDetail = job.detail;
  logger.debug(
    'dispatchAndExecuteTask',
    '',
    getChatMessages('0-{{lastMessageId}}', { include_swipes: true }),
  );
  // 
  const actionsTaken: ActionsTaken = {
    rollback: false,
    apply: false,
    resync: false,
    api: false,
    apiWrite: false,
    editedResync: false,
    swipedRollback: false,
  };

  try {
    // ****: 
    const initialContent = await getMessageContentWithRetry();
    if (initialContent === null) {
      //logger.warn('dispatchAndExecuteTask', 'ERA ');
      //return null;
    }

    const latestMessage = getChatMessages(-1, { include_swipes: true })?.[0];
    if (!latestMessage) {
      logger.warn('dispatchAndExecuteTask', '');
      return null;
    }

    let mk: string;
    let msgId: number | null;
    // let isNewKey: boolean;

    //  API  combo_swipe_and_regenerate  MK 
    //  era:forceSync  API  MK 
    const isApiBypassEvent = getEventGroup(eventType) === 'API' && eventType !== ERA_API_EVENTS.FORCE_SYNC;

    if (eventType === 'combo_swipe_and_regenerate' || isApiBypassEvent) {
      logger.log('dispatchAndExecuteTask', ` ${eventType}  MK `);
      msgId = latestMessage.message_id;
      mk = ''; // 
      // isNewKey = false;
    } else {
      // ****:  MK 
      logger.log('dispatchAndExecuteTask', ' ensureMkForLatestMessage');
      const { mk: newMk, message_id: newMsgId, isNewKey: newIsNewKey } = await ensureMkForLatestMessage(latestMessage);

      if (!newMk || newMsgId === null) {
        logger.warn('dispatchAndExecuteTask', ' MK  ID');
        return null;
      }
      mk = newMk;
      msgId = newMsgId;
      // isNewKey = newIsNewKey;

      // ****:  AI 
      logger.log('dispatchAndExecuteTask', ' ensurePlaceholder');
      await ensurePlaceholder(msgId);
    }

    if (msgId === null) {
      logger.warn('dispatchAndExecuteTask', ' ID');
      return null;
    }
    logContext.mk = mk;
    message_id = msgId;

    const is_user = isUserMessage(latestMessage);

    // //  ensureMkForLatestMessage  MK
    // if (isNewKey && mk) {
    //   mkToIgnore = {
    //     mk: mk,
    //     ignoreCount: RENDER_EVENTS_TO_IGNORE_AFTER_MK_INJECTION,
    //   };
    // }

    // // ****:  MK 
    // logger.debug('dispatchAndExecuteTask', ' handleRedundantRenderEvent');
    // const { shouldSkip, newIgnoreRule } = handleRedundantRenderEvent(eventType, mk, mkToIgnore);
    // mkToIgnore = newIgnoreRule; // 
    // if (shouldSkip) {
    //   // 
    //   return mkToIgnore;
    // }

    logger.log('dispatchAndExecuteTask', `: ${eventType} (: ${eventGroup})`);

    // ****
    // v3.1 payload 
    const payload: DispatcherPayload = {
      mk: mk,
      message_id: message_id,
      is_user: is_user,
      actions: actionsTaken,
      consecutiveProcessingCount: 1, // 
    };

    switch (eventGroup) {
      case 'INIT':
        logger.log('dispatchAndExecuteTask', ' initializeExternalSettings');
        initializeExternalSettings();
        // swipe
        payload.consecutiveProcessingCount = updateConsecutiveMkCount();
        logger.log('dispatchAndExecuteTask', ' handleSyncEvent for INIT');
        await handleSyncEvent(job, actionsTaken, payload);
        break;
      case 'SYNC':
        if (eventType === ERA_EVENT_EMITTER.API_WRITE) {
          actionsTaken.apiWrite = true;
        }
        payload.consecutiveProcessingCount = updateConsecutiveMkCount();
        logger.log('dispatchAndExecuteTask', ' handleSyncEvent for SYNC');
        await handleSyncEvent(job, actionsTaken, payload);
        break;
      case 'API':
        logger.log('dispatchAndExecuteTask', ' handleApiEvent');
        handleApiEvent(job, actionsTaken, payload);
        break;
      case 'UPDATE_MK_ONLY':
        logger.log('dispatchAndExecuteTask', ' handleUpdateMkOnlyEvent');
        await handleUpdateMkOnlyEvent(mk);
        break;
      case 'DIRECTED_SYNC': {
        payload.consecutiveProcessingCount = updateConsecutiveMkCount();

        if (eventType === 'combo_swipe_and_regenerate') {
          // 
          actionsTaken.swipedRollback = true;
          const targetedMessageId = message_id - 1; // 
          const stopAtTarget = true; // 

          logger.log(
            'dispatchAndExecuteTask',
            ` combo_swipe_and_regenerate  ID: ${targetedMessageId}, stopAtTarget: ${stopAtTarget}`,
          );

          if (targetedMessageId < 0) {
            logger.warn('dispatchAndExecuteTask', '');
            await handleSyncEvent(job, actionsTaken, payload);
          } else {
            await handleSyncEvent(job, actionsTaken, payload, targetedMessageId, stopAtTarget);
          }
        } else if (eventType === tavern_events.MESSAGE_EDITED) {
          // 
          actionsTaken.editedResync = true;
          const targetedMessageId = extractMessageIdFromDetail(eventDetail);
          const stopAtTarget = false; // 

          logger.log(
            'dispatchAndExecuteTask',
            ` MESSAGE_EDITED  ID: ${targetedMessageId}, stopAtTarget: ${stopAtTarget}`,
          );

          if (targetedMessageId === null || targetedMessageId < 0) {
            logger.warn('dispatchAndExecuteTask', 'MESSAGE_EDITED  message_id', {
              eventDetail,
            });
            await handleSyncEvent(job, actionsTaken, payload);
          } else {
            await handleSyncEvent(job, actionsTaken, payload, targetedMessageId, stopAtTarget);
          }
        } else {
          // 
          logger.warn(
            'dispatchAndExecuteTask',
            ` DIRECTED_SYNC : ${eventType}`,
          );
          await handleSyncEvent(job, actionsTaken, payload);
        }
        break;
      }
    }
  } catch (error) {
    logger.error('dispatchAndExecuteTask', ` ${eventType} : ${error}`, error);
  } finally {
    // 
    logContext.mk = '';

    // ****: 
    //
    //await new Promise(resolve => setTimeout(resolve, 50));
  }
  // logger.debug(
  //   'dispatchAndExecuteTask',
  //   '',
  //   getChatMessages('0-{{lastMessageId}}', { include_swipes: true }),
  // );
  return null;
}
