/**
 * @file ERA  - 
 * @description
 *  MK stat
 */

'use strict';

import { LOGS_PATH, SEL_PATH } from '../utils/constants';
import { getEraData } from '../utils/era_data';
import { Logger } from '../utils/log';
import { calculateAllStatsBetweenMks, calculateStatAt } from './timetravel';

const logger = new Logger();

/**
 *  MK stat
 * 
 *
 * @param {string} targetMK -  (MK)
 * @returns {object | null} -  stat  MK  null
 */
export function getStatAtMK(targetMK: string): object | null {
  try {
    const { meta } = getEraData();
    const selectedMks: (string | null)[] = _.get(meta, SEL_PATH, []);
    const allLogs: { [mk: string]: string } = _.get(meta, LOGS_PATH, {});

    if (!targetMK) {
      logger.error('getStatAtMK', ` MK `, { targetMK });
      return null;
    }

    //  targetMK 
    if (!selectedMks.includes(targetMK)) {
      logger.error('getStatAtMK', ` targetMK "${targetMK}"  selectedMks `, {
        targetMK,
        selectedMks,
      });
      return null;
    }

    //  timetravel 
    const statAtTarget = calculateStatAt(targetMK, selectedMks, allLogs);

    return statAtTarget;
  } catch (error: any) {
    logger.error('getStatAtMK', `: ${error?.message || error}`, {
      error,
      targetMK,
      eraData: getEraData(),
    });
    return null;
  }
}

/**
 *  MK stat 
 * 
 * @param {string} [startMk] -  MK
 * @param {string} [endMk] -  MK
 * @returns {{mk: string, message_id: number, stat: object}[] | null} -  null
 */
export function getStatsBetweenMKs(
  startMk?: string,
  endMk?: string,
): { mk: string; message_id: number; stat: object }[] | null {
  try {
    const { meta } = getEraData();
    const selectedMks: (string | null)[] = _.get(meta, SEL_PATH, []);
    const allLogs: { [mk: string]: string } = _.get(meta, LOGS_PATH, {});

    const validMks = selectedMks.filter((mk): mk is string => !!mk);
    if (validMks.length === 0) {
      return [];
    }

    //  startMk 
    if (startMk && !validMks.includes(startMk)) {
      logger.error('getStatsBetweenMKs', ` startMk "${startMk}"  selectedMks `, {
        startMk,
        validMks,
      });
      return null;
    }

    //  endMk 
    if (endMk && !validMks.includes(endMk)) {
      logger.error('getStatsBetweenMKs', ` endMk "${endMk}"  selectedMks `, {
        endMk,
        validMks,
      });
      return null;
    }

    const actualStartMk = startMk || validMks[0];
    const actualEndMk = endMk || validMks[validMks.length - 1];

    const results = calculateAllStatsBetweenMks(actualStartMk, actualEndMk, selectedMks, allLogs);
    return results;
  } catch (error: any) {
    logger.error('getStatsBetweenMKs', `: ${error?.message || error}`, {
      error,
      startMk,
      endMk,
      eraData: getEraData(),
    });
    return null;
  }
}
