/**
 * 
 * @param messageId ID
 * @returns  Promise,  resolve
 */
async function forceRenderMessage(messageId: number): Promise<void> {
  const messages = getChatMessages(messageId);

  if (!messages || messages.length === 0) {
    log.warn('forceRenderMessage', `ID ${messageId} `);
    return;
  }

  const message = messages[0];

  //  setChatMessages 
  await setChatMessages([{ message_id: messageId, message: message.message }]);
  log.debug('forceRenderMessage', ` setChatMessages  ${messageId}`);
}

/**
 * N, 
 * 
 */
export async function forceRenderRecentMessages() {
  const scriptVars = getVariables({ type: 'script', script_id: getScriptId() });

  // 
  const forceReload = _.get(scriptVars, '', false);
  if (!forceReload) {
    log.debug('forceRenderRecentMessages', ', ');
    return; // 
  }

  // 1
  const messageCount = _.get(scriptVars, '', 1);
  log.log('forceRenderRecentMessages', `, : ${messageCount}`);

  // , 
  await new Promise(resolve => setTimeout(resolve, 1000));

  const allMessages = getChatMessages('0-{{lastMessageId}}');
  if (!allMessages || allMessages.length === 0) {
    log.warn('forceRenderRecentMessages', ', ');
    return;
  }

  // 
  const recentMessages = allMessages.slice(-messageCount);

  for (const message of recentMessages) {
    log.debug('forceRenderRecentMessages', `: ${message.message_id}`);
    await forceRenderMessage(message.message_id);
    // , UI
    await new Promise(resolve => setTimeout(resolve, 100));
  }
  log.log('forceRenderRecentMessages', '');
}
