import { updateScriptSettings, initEraSettings } from '../../utils/era_data';
import { Logger } from '../../utils/log';

const logger = new Logger();

/**
 * 
 * 
 */
export async function initializeExternalSettings() {
  //  ref 
  initEraSettings();
  logger.log('initializeExternalSettings', '...');
  await updateScriptSettings(async settings => {
    // updateScriptSettings 
    logger.debug('initializeExternalSettings', ':', settings);
    return settings;
  });
  logger.log('initializeExternalSettings', '');
}

//  app_ready 
$(() => {
  initializeExternalSettings();
});
