radius: 10px; /*  */\n  border: 1px solid var(--snapshot-stroke); /*  */\n  background: var(--snapshot-bg-gradient), var(--snapshot-bg); /*  */\n  backdrop-filter: saturate(1.05) blur(2px); /*  */\n  color: var(--snapshot-text); /*  */\n  font-family: inherit; /*  */\n  font-size: 14px; /*  */\n  line-height: 1.2; /*  */\n  cursor: pointer; /*  */\n  -webkit-appearance: none; /* Safari/Chrome */\n  -moz-appearance: none; /* Firefox */\n  appearance: none; /*  */\n  transition:\n    border-color 0.18s ease,\n    box-shadow 0.18s ease,\n    background 0.25s ease; /*  */\n  box-shadow: var(--snapshot-inner-shadow), var(--snapshot-outer-shadow); /*  */\n}\n\n/* ----  ---- */\n#era-snapshot-ui .mk-selector:hover {\n  /*  */\n  border-color: color-mix(in oklab, var(--snapshot-stroke), var(--snapshot-accent) 22%); /*  */\n}\n\n/* ---- / +  ---- */\n#era-snapshot-ui .mk-selector:focus {\n  /*  */\n  outline: none; /*  */\n  border-color: var(--snapshot-accent); /*  */\n  box-shadow:\n    0 0 0 3px var(--snapshot-accent-soft),\n    /*  */ var(--snapshot-focus-inner-shadow); /*  */\n}\n#era-snapshot-ui .mk-selector:focus-visible {\n  /*  */\n  outline: none; /*  */\n}\n\n/* ----  ---- */\n#era-snapshot-ui .mk-selector option {\n  /*  */\n  color: var(--snapshot-text); /*  */\n  background: var(--snapshot-bg); /*  */\n}\n\n/* ----  /  ---- */\n#era-snapshot-ui .loading-indicator,\n#era-snapshot-ui .error-message {\n  /*  */\n  padding: 18px; /*  */\n  text-align: center; /*  */\n  border-radius: 12px; /*  */\n  border: 1px dashed var(--line, rgba(0, 0, 0, 0.12)); /*  */\n  background: var(--bg-empty, rgba(0, 0, 0, 0.03)); /*  */\n  color: var(--text-normal, #2a2a2a); /*  */\n}\n\n/* ----  ---- */\n#era-snapshot-ui .error-message {\n  /*  snapshot danger  */\n  color: var(--snapshot-danger);\n  background: var(--snapshot-danger-soft);\n  border-color: color-mix(in oklab, var(--snapshot-danger), transparent 55%);\n}\n\n/* ----  ---- */\n@media (prefers-reduced-motion: reduce) {\