/**
 * @file ERA  - 
 * @description
 *  MK stat
 *  `variables` 
 *
 * ****:
 * - ****: stat `EditLog` 
 * - ****:  `EditLog` 
 * - ****: `EditLog`  MK  `selectedMks` 
 */

'use strict';

import { parseEditLog } from '../utils/data';
import { Logger } from '../utils/log';

const logger = new Logger();

// ==================================================================
//  ( rollback.ts  sync.ts)
// ==================================================================

/**
 *  stat  EditLog
 * @param stat - 
 * @param log -  EditLog 
 * @returns {object} -  stat 
 */
function applyRollback(stat: object, log: any[]): object {
  if (!log || !log.length) return stat;
  //  EditLog 
  for (let i = log.length - 1; i >= 0; i--) {
    const entry = log[i];
    const op = String(entry?.op || '').toLowerCase();
    const path = String(entry?.path || '');
    if (!path) continue;

    if (op === 'insert') {
      // 
      _.unset(stat, path);
    } else if (op === 'update' || op === 'delete') {
      // 
      if (typeof entry.value_old === 'undefined') {
        _.unset(stat, path);
      } else {
        _.set(stat, path, _.cloneDeep(entry.value_old));
      }
    }
  }
  return stat;
}

/**
 *  stat  EditLog
 * @param stat - 
 * @param log -  EditLog 
 * @returns {object} -  stat 
 */
function applyForward(stat: object, log: any[]): object {
  if (!log || !log.length) return stat;
  //  log
  for (const entry of log) {
    if (!entry || !entry.path) continue;
    const op = String(entry?.op || '').toLowerCase();

    if (op === 'insert' || op === 'update') {
      _.set(stat, entry.path, _.cloneDeep(entry.value_new));
    } else if (op === 'delete') {
      _.unset(stat, entry.path);
    }
  }
  return stat;
}

// ==================================================================
