// ==================================================================

/**
 * ****:  stat  currentMK  targetMK 
 *
 * @param {string} targetMK -  MK
 * @param {string} currentMK -  MK
 * @param {object} currentStat - 
 * @param {string[]} selectedMks -  MK 
 * @param {{ [mk: string]: string }} allLogs -  EditLog 
 * @returns {object} -  stat 
 */
export function rollbackStatToMK(
  targetMK: string | null | undefined,
  currentMK: string,
  currentStat: object,
  selectedMks: (string | null)[],
  allLogs: { [mk: string]: string },
): object {
  const currentIndex = selectedMks.indexOf(currentMK);
  //  targetMK  null  undefined,  targetIndex  -1
  const targetIndex = targetMK ? selectedMks.indexOf(targetMK) : -1;

  if (currentIndex === -1) {
    const errorMsg = `MK: ${currentMK}`;
    logger.error('rollbackStatToMK', errorMsg, { currentMK, selectedMks });
    throw new Error(errorMsg);
  }
  if (targetMK && targetIndex === -1) {
    const errorMsg = `MK: ${targetMK}`;
    logger.error('rollbackStatToMK', errorMsg, { targetMK, selectedMks });
    throw new Error(errorMsg);
  }

  if (targetIndex > currentIndex) {
    logger.warn('rollbackStatToMK', 'MK', {
      targetMK,
      currentMK,
      targetIndex,
      currentIndex,
    });
    return _.cloneDeep(currentStat);
  }
  if (targetIndex === currentIndex) {
    return _.cloneDeep(currentStat);
  }

  let tempStat = _.cloneDeep(currentStat);

  // 
  //  targetIndex  -1,  i > -1,  i=0, 
  for (let i = currentIndex; i > targetIndex; i--) {
    const mkToRollback = selectedMks[i];
    if (!mkToRollback) continue;
    const log = parseEditLog(allLogs[mkToRollback]);
    tempStat = applyRollback(tempStat, log);
  }

  return tempStat;
}

/**
 * ****:  stat  mk  mk 
 *
 * @param {object} startStat - 
 * @param {string} startMK -  MK
 * @param {string} targetMK -  MK
 * @param {string[]} selectedMks -  MK 
 * @param {{ [mk: string]: string }} allLogs -  EditLog 
 * @returns {object} -  MK  stat
 */
export function calculateStatForward(
  startStat: object,
  startMK: string,
  targetMK: string,
  selectedMks: (string | null)[],
  allLogs: { [mk: string]: string },
): object {
  const startIndex = selectedMks.indexOf(startMK);
  const targetIndex = selectedMks.indexOf(targetMK);

  if (startIndex === -1 || targetIndex === -1) {
    const errorMsg = `MKMK: ${startMK}, MK: ${targetMK}`;
    logger.error('calculateStatForward', errorMsg, { startMK, targetMK, selectedMks });
    throw new Error(errorMsg);
  }

  if (targetIndex < startIndex) {
    logger.warn('calculateStatForward', 'MK', {
      startMK,
      targetMK,
      startIndex,
      targetIndex,
    });
    return _.cloneDeep(startStat);
  }
  if (targetIndex === startIndex) {
    return _.cloneDeep(startStat);
  }

  let tempStat = _.cloneDeep(startStat);

  // 
  for (let i = startIndex + 1; i <= targetIndex; i++) {
    const mkToApply = selectedMks[i];
    if (!mkToApply) continue;
    const log = parseEditLog(allLogs[mkToApply]);
    tempStat = applyForward(tempStat, log);
  }

  return tempStat;
}

/**
 * ****:  MK 
 * 
 *
 * @param {string} targetMK -  MK
 * @param {string[]} selectedMks -  MK 
 * @param {{ [mk: string]: string }} allLogs -  EditLog 
 * @returns {object} -  stat 
 */
export function calculateStatAt(
  targetMK: string,
  selectedMks: (string | null)[],
  allLogs: { [mk: string]: string },
): object {
  const targetIndex = selectedMks.indexOf(targetMK);

  if (targetIndex === -1) {
    const errorMsg = `MK: ${targetMK}`;
    logger.error('calculateStatAt', errorMsg, { targetMK, selectedMks });
    throw new Error(errorMsg);
  }

  let tempStat = {}; // 

  //  targetIndex
  for (let i = 0; i <= targetIndex; i++) {
    const mkToApply = selectedMks[i];
    if (!mkToApply) continue;
    const log = parseEditLog(allLogs[mkToApply]);
    tempStat = applyForward(tempStat, log);
  }

  return tempStat;
}

/**
 * ****:  startMk  endMk  stat 
 * 
 *
 * @param {string} startMk -  MK
 * @param {string} endMk -  MK
 * @param {(string | null)[]} selectedMks -  MK 
 * @param {{ [mk: string]: string }} allLogs -  EditLog 
 * @returns {{mk: string, message_id: number, stat: object}[]} - 
 */
export function calculateAllStatsBetweenMks(
  startMk: string,
  endMk: string,
  selectedMks: (string | null)[],
  allLogs: { [mk: string]: string },
): { mk: string; message_id: number; stat: object }[] {
  const startIndex = selectedMks.indexOf(startMk);
  const endIndex = selectedMks.indexOf(endMk);

  if (startIndex === -1 || endIndex === -1 || startIndex > endIndex) {
    logger.error('calculateAllStatsBetweenMks', ' startMk  endMk', {
      startMk,
      endMk,
      selectedMks,
    });
    return [];
  }

  // 1. 
  let currentStat = calculateStatAt(startMk, selectedMks, allLogs);
  const results: { mk: string; message_id: number; stat: object }[] = [
    {
      mk: startMk,
      message_id: startIndex,
      stat: _.cloneDeep(currentStat),
    },
  ];

  // 2. 
  for (let i = startIndex + 1; i <= endIndex; i++) {
    const mk = selectedMks[i];
    if (!mk) continue;

    const log = parseEditLog(allLogs[mk]);
    currentStat = applyForward(currentStat, log);
    results.push({
      mk: mk,
      message_id: i,
      stat: _.cloneDeep(currentStat),
    });
  }

  return results;
}

/**
 * ****
 *  `selectedMks`  (`value_new`)
 *  `update`  `value_old`
 *
 * @param {string} path - 
 * @param {number} startMessageId -  ID ( selectedMks ) ****
 * @param {string[]} selectedMks -  MK 
 * @param {{ [mk: string]: string }} allLogs -  EditLog  MK
 * @returns {any}  `value_new` `null`
 */
export function findLatestNewValue(
  path: string,
  startMessageId: number,
  selectedMks: (string | null)[],
  allLogs: { [mk: string]: string },
): any {
  logger?.debug('findLatestNewValue', ` <${path}>  <${startMessageId}> ...`);

  if (startMessageId < 0 || startMessageId > selectedMks.length) {
    logger?.warn('findLatestNewValue', ` ${startMessageId} `, {
      path,
      startMessageId,
      selectedMksCount: selectedMks.length,
    });
    return null;
  }

  // 1.  `selectedMks` 
  for (let i = startMessageId - 1; i >= 0; i--) {
    const mk = selectedMks[i];
    if (!mk) continue;

    // 2.  mk  allLogs  mk  EditLog
    const editLogForMkRaw = allLogs[mk];
    const editLogForMk = parseEditLog(editLogForMkRaw);

    if (!editLogForMk || editLogForMk.length === 0) continue;

    // 3.  MK  EditLog
    for (let j = editLogForMk.length - 1; j >= 0; j--) {
      const logEntry = editLogForMk[j];
      if (!logEntry || !logEntry.path) continue;

      // Case 1: 
      if (logEntry.path === path) {
        if (logEntry.op === 'delete') {
          logger?.warn(
            'findLatestNewValue',
            `>>  MK:${mk}  <${path}>  'delete'  null`,
          );
          return null;
        }
        logger?.debug(
          'findLatestNewValue',
          `>> !  MK:${mk}  <${path}> : `,
          logEntry.value_new,
        );
        return _.cloneDeep(logEntry.value_new);
      }

      // Case 2:  ( logEntry.path )
      // ,  "a.b.c.d",  "a.b" 
      // `_.get`  ( "c.d")
      if (path.startsWith(logEntry.path + '.')) {
        const subPath = path.substring(logEntry.path.length + 1);
        const parentNewVal = logEntry.value_new;
        if (_.isPlainObject(parentNewVal) && _.has(parentNewVal, subPath)) {
          const foundVal = _.get(parentNewVal, subPath);
          logger?.debug(
            'findLatestNewValue',
            `>> !  MK:${mk}  <${logEntry.path}>,  <${subPath}> :`,
            foundVal,
          );
          return _.cloneDeep(foundVal);
        }
      }
    }
  }

  // 
  logger?.debug('findLatestNewValue', ` ${path}  null `);
  return null;
}
