import { computed, defineComponent, onMounted, ref, watch } from 'vue'; // /
import { Logger } from '../../utils/log';
export default defineComponent({
    name: 'JsonNode', // 
    props: {
        k: { type: [String, Number], required: true }, // 
        v: { required: true }, // 
        path: { type: String, required: true }, //  key/
        level: { type: Number, required: true }, // 
        defaultCollapsed: { type: Boolean, default: true }, // 
        maxDepth: { type: Number, default: 3 }, // 
    },
    setup(p) {
        const logger = new Logger("ERA\u53D8\u91CF\u6846\u67B6/ui/template/JsonNode");
        onMounted(() => {
            //logger.debug('onMounted', '', { props: p });
        });
        // 
        const type = computed(() => {
            const val = p.v;
            if (val === null)
                return 'null';
            if (Array.isArray(val))
                return 'array';
            const t = typeof val;
            if (t === 'object')
                return 'object';
            if (t === 'undefined')
                return 'undefined';
            return t;
        });
        // /
        const isArray = computed(() => type.value === 'array');
        const isObject = computed(() => type.value === 'object');
        // 
        const foldable = computed(() => isArray.value || isObject.value);
        //  maxDepth  defaultCollapsed
        const open = ref(p.level <= (p.maxDepth ?? 3) ? true : !p.defaultCollapsed);
        watch(open, newOpenState => {
            logger.debug('watch:open', `: ${newOpenState ? '' : ''}`);
        });
        //  undefined / bigint / symbol
        const primitiveText = computed(() => {
            const val = p.v;
            switch (type.value) {
                case 'string':
                    return JSON.stringify(val);
                case 'number':
                    return String(val);
                case 'boolean':
                    return val ? 'true' : 'false';
                case 'null':
                    return 'null';
                case 'undefined':
                    return 'undefined';
                case 'bigint':
                    return String(val) + 'n';
                case 'symbol':
                    return String(val);
                case 'function':
                    return '()';
                default:
                    return '';
            }
        });
        // 
        const summary = computed(() => {
            if (isArray.value)
                return `Array[${p.v.length}]`;
            if (isObject.value)
                return `Object{${Object.keys(p.v || {}).length}}`;
            return '';
        });
        //    entries  
        const childEntries = computed(() => {
            if (isObject.value)
                return p.v;
            if (isArray.value) {
                const out = {};
                p.v.forEach((cv, idx) => {
                    out[String(idx)] = cv;
                });
                return out;
            }
            return {};
        });
        return { type, isArray, foldable, open, primitiveText, summary, childEntries };
    },
});
