/**
 * @file ERA  - 
 * @description
 *  `<VariableEdit>`  `EditLog`
 *
 * ****:
 * - ****: 
 * - ****: `EditLog` `applyEditAtLevel` 
 *   
 * - ****: 
 *   
 */
'use strict';

import { LOGS_PATH, SEL_PATH } from '../../utils/constants';
import { sanitizeArrays } from '../../utils/data';
import { Logger } from '../../utils/log';
import { findLatestNewValue } from '../timetravel';

const logger = new Logger();

/**
 * ** | **
 *  `<VariableEdit>` ****
 *
 * ****:
 * 1. ****
 * 2. ****
 * 3. **** `EditLog`****
 *     `findLatestNewValue`  `EditLog` 
 *    
 *
 * @param {any} statData -  ()
 * @param {string} basePath - 
 * @param {any} patchObj - 
 * @param {any[]} editLog -  ()
 * @param {number} messageId -  ID
 * @param {string[]} selectedMks -  MK 
 * @param {{ [mk: string]: string }} allLogs -  EditLog 
 */
async function applyEditAtLevel(
  statData: any,
  basePath: string,
  patchObj: any,
  editLog: any[],
  messageId: number,
  selectedMks: (string | null)[],
  allLogs: { [mk: string]: string },
) {
  // --- 1.  ---
  const currentNodeInVars = basePath ? _.get(statData, basePath) : statData;
  if (currentNodeInVars === undefined) {
    logger.warn('applyEditAtLevel', `VariableEdit  -> ${basePath || '(root)'}`, {
      basePath,
      patchObj,
    });
    return;
  }

  // --- 2.  (`updatable`) ---
  //  updatable  true ()
  const isUpdatable = _.get(currentNodeInVars, ['$meta', 'updatable'], true);

  //  updatable  false  true 
  const isBypassingProtection =
    isUpdatable === false && // 
    _.get(patchObj, ['$meta', 'updatable']) === true; //  true

  // 
  if (isUpdatable === false && !isBypassingProtection) {
    logger.warn(
      'applyEditAtLevel',
      `VariableEdit  <${basePath}>  "$meta.updatable: false" `,
      { basePath, patchObj, currentNodeInVars },
    );
    return; // 
  }

  // --- 3.  ---
  // 
  for (const key of Object.keys(patchObj)) {
    const subPath = basePath ? `${basePath}.${key}` : key;
    const valNew = patchObj[key];

    // ****
    // 
    if (_.isPlainObject(valNew)) {
      await applyEditAtLevel(statData, subPath, valNew, editLog, messageId, selectedMks, allLogs);
      continue; // 
    }

    // ** ()**
    // 

    // 
    if (!_.has(statData, subPath)) {
      logger.warn('applyEditAtLevel', `VariableEdit  -> ${subPath}`, {
        subPath,
        valNew,
        statData,
      });
      continue;
    }

    // a.  (`valOld`)
    // 
    logger.debug('applyEditAtLevel', `[]  <${subPath}>  ID <${messageId}> ...`);
    let valOld = findLatestNewValue(subPath, messageId, selectedMks, allLogs);
    if (valOld === null) {
      // statData 
      //  <VariableEdit> 
      valOld = _.get(statData, subPath);
      logger.debug(
        'applyEditAtLevel',
        `[]  stat_data : ${JSON.stringify(valOld)}`,
      );
    } else {
      logger.debug('applyEditAtLevel', `[] : ${JSON.stringify(valOld)}`);
    }

    const cleaned = sanitizeArrays(valNew); // 

    // b. 
    //  EditLog
    // 
    _.set(statData, subPath, cleaned);
    editLog.push({
      op: 'update',
      path: subPath,
      value_old: _.cloneDeep(valOld),
      value_new: _.cloneDeep(cleaned),
    });
  }
}

/**
 *  `<VariableEdit>` 
 * 
 *
 * @param {any[]} allEdits -  edit 
 * @param {number} messageId -  ID
 * @param {any} initialStat -  `stat` 
 * @param {any} meta -  `selectedMks`  `allLogs` 
 * @returns {Promise<{ finalStat: any, editLog: any[] }>} 
 */
export async function processEditBlocks(
  allEdits: any[],
  messageId: number,
  initialStat: any,
  meta: any,
): Promise<{ finalStat: any; editLog: any[] }> {
  if (!allEdits || allEdits.length === 0) {
    return { finalStat: _.cloneDeep(initialStat), editLog: [] };
  }

  // 
  const finalStat = _.cloneDeep(initialStat);
  const editLog: any[] = [];

  const selectedMks: string[] = _.get(meta, SEL_PATH, []);
  const allLogs: { [mk: string]: string } = _.get(meta, LOGS_PATH, {});

  //  <VariableEdit> 
  for (const editRoot of allEdits) {
    if (!_.isPlainObject(editRoot) || _.isEmpty(editRoot)) continue;
    try {
      logger.debug('processEditBlocks', ` editRoot: `, editRoot);
      //  '' 
      // applyEditAtLevel  finalStat  editLog
      await applyEditAtLevel(finalStat, '', editRoot, editLog, messageId, selectedMks, allLogs);
    } catch (e: any) {
      logger.error('processEditBlocks', ` editRoot : ${e?.message || e}`, {
        error: e,
        editRoot,
        finalStat,
      });
    }
  }

  logger.debug('processEditBlocks', ' VariableEdit ');
  return { finalStat, editLog };
}
