ltCollapsed\"\n          :max-depth=\"maxDepth\"\n        />\n        <div class=\"json-line\" :style=\"{ paddingLeft: level * 14 + 'px' }\">\n          <span class=\"brace\">{{ isArray ? ']' : '}' }}</span>\n          <!--  -->\n        </div>\n      </div>\n    </template>\n  </div>\n</template>\n\n<script lang=\"ts\">\nimport { computed, defineComponent, onMounted, ref, watch } from 'vue'; // /\nimport { Logger } from '../../utils/log';\n\nexport default defineComponent({\n  name: 'JsonNode', // \n  props: {\n    k: { type: [String, Number], required: true }, // \n    v: { required: true }, // \n    path: { type: String, required: true }, //  key/\n    level: { type: Number, required: true }, // \n    defaultCollapsed: { type: Boolean, default: true }, // \n    maxDepth: { type: Number, default: 3 }, // \n  },\n  setup(p) {\n    const logger = new Logger();\n\n    onMounted(() => {\n      //logger.debug('onMounted', '', { props: p });\n    });\n\n    // \n    const type = computed(() => {\n      const val = p.v;\n      if (val === null) return 'null';\n      if (Array.isArray(val)) return 'array';\n      const t = typeof val;\n      if (t === 'object') return 'object';\n      if (t === 'undefined') return 'undefined';\n      return t as 'string' | 'number' | 'boolean' | 'bigint' | 'symbol' | 'function';\n    });\n\n    // /\n    const isArray = computed(() => type.value === 'array');\n    const isObject = computed(() => type.value === 'object');\n\n    // \n    const foldable = computed(() => isArray.value || isObject.value);\n\n    //  maxDepth  defaultCollapsed\n    const open = ref<boolean>(p.level <= (p.maxDepth ?? 3) ? true : !p.defaultCollapsed);\n\n    watch(open, newOpenState => {\n      logger.debug('watch:open', `: ${newOpenState ? '' : ''}`);\n    });\n\n    //  undefined / bigint / symbol\n    const primitiveText = computed(() => {\n      const val = p.v as any;\n      switch (type.value) {\n        case 'string':\n          return JSON.stringify(val);\n        case 'number':\n          return String(val);\n        case 'boolean':\n          return val ? 'true' : 'false';\n        case 'null':\n          return 'null';\n        case 'undefined':\n          return 'undefined';\n        case 'bigint':\n          return String(val) + 'n';\n        case 'symbol':\n          return String(val);\n        case 'function':\n          return '()';\n        default:\n          return '';\n      }\n    });\n\n    // \n    const summary = computed(() => {\n      if (isArray.value) return `Array[${(p.v as any[]).length}]`;\n      if (isObject.value) return `Object{${Object.keys(p.v || {}).length}}`;\n      return '';\n    });\n\n    //    entries  \n    const childEntries = computed<Record<string, any>>(() => {\n      if (isObject.value) return p.v as Record<string, any>;\n      if (isArray.value) {\n        const out: Record<string, any> = {};\n        (p.v as any[]).forEach((cv, idx) => {\n          out[String(idx)] = cv;\n        });\n        return out;\n      }\n      return {};\n    });\n\n    return { type, isArray, foldable, open, primitiveText, summary, childEntries };\n  },\n});\n</script>\n\n<style scoped>\n.json-line {\n  position: relative;\n  display: flex;\n  align-items: center;\n  gap: 6px;\n  padding: 2px 6px;\n  border-radius: 6px;\n  transition: background 0.2s ease;\n}\n.json-line:hover {\n  background: var(--json-hover-bg);\n}\n.json-children {\n  border-left: 1px dashed var(--json-tree-line);\n  transition: border-color 0.3s ease;\n}\n\n/*  */\n.key {\n  color: var(--json-key-color);\n  font-weight: 700;\n  transition: color 0.3s ease;\n}\n.colon {\n  color: var(--text-normal);\n  transition: color 0.3s ease;\n}\n.brace {\n  color: var(--text-normal);\n  transition: color 0.3s ease;\n}\n.summary {\n  color: var(--text-normal);\n  margin-left: 4px;\n  transition: color 0.3s ease;\n}\n.val.string {\n  color: var(--json-val-string);\n}\n.val.number {\n  color: var(--json-val-number);\n}\n.val.boolean {\n  color: var(--json-val-boolean);\n}\n.val.null {\n  color: var(--json-val-null);\n}\n.val.undefined {\n  color: var(--json-val-null);\n}\n\n/*  */\n.caret {\n  width: 18px;\n  height: 18px;\n  border: 1px solid var(--border-soft);\n  border-radius: 4px;\n  background: var(--bg-solid);\n  line-height: 16px;\n  text-align: center;\n  font-size: 10px;\n  color: var(--text-normal);\n  cursor: pointer;\n  box-shadow: var(--json-caret-shadow);\n  transition:\n    transform 0.18s ease,\n    box-shadow 0.18s ease,\n    background 0.3s ease,\n    border-color 0.3s ease,\n    color 0.3s ease;\n}\n.caret.open {\n  transform: rotate(90deg);\n  box-shadow: var(--json-caret-open-shadow);\n}\n\n/* ===[]  === */\n.node {\n  /*  */\n  overflow: clip; /*  */\n  contain: paint; /*  1px  */\n  min-height: 0; /*  */\n}\n\n.json-children {\n  /*  */\n  overflow: clip; /*  */\n  contain: paint; /* / */\n}\n</style>\n"],"sourceRoot":""}]);
