on class=\"card-header\" :aria-expanded=\"isOpen\" @click=\"onToggle\">\n      <!--  -->\n      <span class=\"title\">ERA </span>\n      <!--  -->\n      <span class=\"chev\" aria-hidden=\"true\">{{ isOpen ? '' : '' }}</span>\n      <!--  -->\n    </button>\n\n    <!--  -->\n    <div v-show=\"isOpen\" class=\"content\">\n      <!--  -->\n      <!-- // -->\n      <div class=\"toolbar\">\n        <!--  -->\n        <button class=\"mini-btn\" title=\"\" @click.stop=\"loadVars\"> </button>\n        <!--  -->\n        <div class=\"spacer\"></div>\n        <!--  -->\n        <button class=\"mini-btn subtle\" title=\"\" @click.stop=\"discardEdits\"> </button>\n        <!--  -->\n        <button class=\"mini-btn primary\" title=\"\" @click.stop=\"saveEdits\"> </button>\n        <!--  -->\n      </div>\n\n      <!--  -->\n      <div class=\"vars\">\n        <template v-if=\"rows.length > 0\">\n          <!--  -->\n          <div v-for=\"row in rows\" :key=\"row.key\" class=\"var-row\">\n            <!--  -->\n            <label class=\"var-key\" :title=\"row.key\">{{ row.key }}</label>\n            <!--  -->\n            <!--  -->\n            <div class=\"var-editor\">\n              <!--  -->\n              <!--  -->\n              <template v-if=\"row.type === 'boolean'\">\n                <label class=\"switch\">\n                  <!--  -->\n                  <input v-model=\"edits[row.key]\" type=\"checkbox\" />\n                  <!--  -->\n                  <span class=\"track\"></span>\n                  <!--  -->\n                </label>\n              </template>\n\n              <!-- number  -->\n              <template v-else-if=\"row.type === 'number'\">\n                <input v-model.number=\"edits[row.key]\" type=\"number\" class=\"ipt\" :placeholder=\"String(row.value)\" />\n                <!--  -->\n              </template>\n\n              <!-- text  -->\n              <template v-else-if=\"row.type === 'string'\">\n                <input v-model=\"edits[row.key]\" type=\"text\" class=\"ipt\" :placeholder=\"String(row.value)\" />\n                <!--  -->\n              </template>\n\n              <!-- //null/JSON  -->\n              <template v-else>\n                <textarea\n                  v-model=\"jsonBuffers[row.key]\"\n                  class=\"ipt ta\"\n                  :placeholder=\"safeStringify(row.value)\"\n                  @input=\"markJsonTouched(row.key)\"\n                ></textarea>\n                <!-- JSON  -->\n                <span class=\"hint\" :class=\"{ ok: jsonState[row.key]?.ok, bad: jsonState[row.key]?.bad }\">{{\n                  jsonState[row.key]?.msg ?? ' JSON '\n                }}</span>\n                <!-- JSON  -->\n              </template>\n            </div>\n            <!--  -->\n            <span class=\"type-chip\">{{ humanType(row.type) }}</span>\n            <!--  -->\n          </div>\n        </template>\n\n        <template v-else>\n          <!--  -->\n          <div class=\"placeholder\">\n            <p></p>\n            <!--  -->\n            <p class=\"dim\">\n               <code>app_ready</code> \n              <code>initializeExternalSettings()</code> \n            </p>\n            <!--  -->\n          </div>\n        </template>\n      </div>\n    </div>\n  </section>\n</template>\n\n<script setup lang=\"ts\">\n/* \n   - \n   -  boolean/number/string/json \n   - \n*/\nimport { reactive, ref } from 'vue'; // Vue  API\nimport toastr from 'toastr';\nimport { z } from 'zod';\nimport { SettingsSchema } from '../../../utils/constants';\nimport { settings, updateScriptSettings } from '../../../utils/era_data';\nimport { Logger } from '../../../utils/log'; // \n\nconst logger = new Logger(); // \n\n// ===  ===\nconst isOpen = ref(false); // \n\n// ===  ===\ntype Row = { key: string; value: any; type: 'boolean' | 'number' | 'string' | 'json' }; // \nconst rows = ref<Row[]>([]); // \nconst edits = reactive<Record<string, any>>({}); // //\nconst jsonBuffers = reactive<Record<string, string>>({}); //  JSON \nconst jsonState = reactive<Record<string, { ok?: boolean; bad?: boolean; msg?: string }>>({}); // JSON \n\n// ===  ===\nfunction detectType(v: any): Row['type'] {\n  // \n  if (typeof v === 'boolean') return 'boolean'; // \n  if (typeof v === 'number' && Number.isFinite(v)) return 'number'; // \n  if (typeof v === 'string') return 'string'; // \n  return 'json'; //  json//null/\n}\nfunction humanType(t: Row['type']): string {\n  // \n  return t === 'boolean' ? '' : t === 'number' ? '' : t === 'string' ? '' : 'JSON'; // \n}\nfunction safeStringify(v: any): string {\n  //  JSON \n  try {\n    return JSON.stringify(v, null, 2);\n  } catch {\n    return String(v);\n  } // \n}\n\n// ===  ===\nfunction loadVars() {\n  try {\n    logger.debug('loadVars', '...');\n    const obj = settings.value;\n    logger.debug('loadVars', ':', obj);\n\n    if (!obj || Object.keys(obj).length === 0) {\n      logger.warn('loadVars', 'UI ');\n      rows.value = [];\n      toastr.info('');\n      return;\n    }\n\n    // \n    rows.value = [];\n    for (const k of Object.keys(edits)) delete edits[k];\n    for (const k of Object.keys(jsonBuffers)) delete jsonBuffers[k];\n    for (const k of Object.keys(jsonState)) delete jsonState[k];\n\n    // \n    Object.entries(obj)\n      .filter(([key]) => key !== '')\n      .forEach(([key, value]) => {\n        const t = detectType(value);\n        rows.value.push({ key, value, type: t });\n        if (t === 'json') {\n          jsonBuffers[key] = safeStringify(value);\n        } else {\n          edits[key] = value;\n        }\n      });\n    toastr.success('', '');\n  } catch (e) {\n    logger.error('loadVars', ':', e);\n    toastr.error(' ERA ', '');\n  }\n}\n\n// ===  ===\nfunction onToggle() {\n  // \n  isOpen.value = !isOpen.value; // \n  if (isOpen.value && rows.value.length === 0) loadVars(); // \n}\n\n// === JSON  +  ===\nfunction markJsonTouched(key: string) {\n  //  JSON \n  const txt = jsonBuffers[key] ?? ''; // \n  if (txt.trim() === '') {\n    // \n    jsonState[key] = { bad: true, ok: false, msg: ' null  null' }; // \n    return; // \n  }\n  try {\n    JSON.parse(txt);\n    jsonState[key] = { ok: true, bad: false, msg: 'JSON ' };\n  } catch (e: any) {\n    // \n    jsonState[key] = { bad: true, ok: false, msg: `JSON ${e?.message ?? ''}` };\n  } // \n}\n\n// ===  ===\nfunction discardEdits() {\n  // \n  logger.log('discardEdits', '');\n  loadVars(); // \n  toastr.info('', '');\n}\n\n// ===  ===\nasync function saveEdits() {\n  // \n  const originalRows = JSON.parse(JSON.stringify(rows.value)); // \n\n  try {\n    // 1.  UI\n    const updatedSettings: Record<string, any> = {};\n    rows.value.forEach(row => {\n      if (row.type === 'json') {\n        if (jsonState[row.key]?.bad) {\n          throw new Error(` ${row.key}  JSON `);\n        }\n        if (jsonBuffers[row.key] != null) {\n          const newValue = JSON.parse(jsonBuffers[row.key]);\n          row.value = newValue;\n          updatedSettings[row.key] = newValue;\n        }\n      } else if (edits[row.key] !== undefined) {\n        row.value = edits[row.key];\n        updatedSettings[row.key] = edits[row.key];\n      }\n    });\n    toastr.success('...', '');\n\n    // 2. \n    await updateScriptSettings(currentSettings => {\n      const next: z.infer<typeof SettingsSchema> = { ...currentSettings };\n\n      // //\n      Object.entries(edits).forEach(([k, v]) => {\n        const key = k as keyof z.infer<typeof SettingsSchema>;\n        (next as any)[key] = v;\n      });\n\n      //  JSON \n      Object.entries(jsonBuffers).forEach(([k, txt]) => {\n        if (txt != null) {\n          if (jsonState[k]?.bad) throw new Error(` ${k}  JSON `);\n          const key = k as keyof z.infer<typeof SettingsSchema>;\n          (next as any)[key] = JSON.parse(txt);\n        }\n      });\n\n      return next;\n    });\n\n    logger.log('saveEdits', '');\n    toastr.success('', '');\n    window.dispatchEvent(new CustomEvent('era-settings-updated'));\n    //  loadVars()\n  } catch (e) {\n    logger.error('saveEdits', '', e);\n    toastr.error(`${(e as any)?.message ?? e}`, '');\n    // 3.  UI\n    rows.value = originalRows;\n    //  edits  jsonBuffers  UI\n    originalRows.forEach((row: Row) => {\n      if (row.type === 'json') {\n        jsonBuffers[row.key] = safeStringify(row.value);\n      } else {\n        edits[row.key] = row.value;\n      }\n    });\n  }\n}\n</script>\n\n<style scoped>\n/* ===  ActionButtons  === */\n.settings-card {\n  /*  */\n  width: 100%; /*  */\n  padding: 12px; /*  */\n  background: var(--settings-bg-glass);\n  border: 1px solid var(--border-strong);\n  border-radius: 16px; /*  */\n  backdrop-filter: blur(10px); /*  */\n  box-shadow: var(--settings-shadow-card), var(--settings-shadow-inset);\n  display: flex; /*  */\n  flex-direction: column; /*  */\n  gap: 10px; /*  */\n  transition:\n    background 0.3s ease,\n    border-color 0.3s ease;\n}\n\n/* / */\n.card-header {\n  /*  */\n  display: flex; /*  */\n  align-items: center; /*  */\n  justify-content: space-between; /*  */\n  padding: 8px 10px; /*  */\n  border-radius: 10px; /*  */\n  border: 1px solid var(--border-soft);\n  background: var(--chip-bg);\n  color: var(--text-title);\n  font-weight: 800; /*  */\n  font-size: 13px; /*  */\n  cursor: pointer; /*  */\n  box-shadow: var(--settings-shadow-inset), var(--shadow-button);\n  transition:\n    background 0.3s ease,\n    border-color 0.3s ease,\n    color 0.3s ease,\n    transform 0.2s ease,\n    box-shadow 0.2s ease;\n}\n.card-header:hover {\n  transform: translateY(-1px);\n  box-shadow: var(--settings-shadow-inset), var(--shadow-button-hover);\n} /*  */\n.title {\n  pointer-events: none;\n} /*  */\n.chev {\n  opacity: 0.7;\n} /*  */\n\n/*  */\n.content {\n  display: flex;\n  flex-direction: column;\n  gap: 10px;\n} /*  */\n\n/*  */\n.toolbar {\n  display: flex;\n  align-items: center;\n  gap: 8px;\n} /*  */\n.spacer {\n  flex: 1;\n} /*  */\n\n.mini-btn {\n  /*  */\n  padding: 6px 10px; /*  */\n  border-radius: 8px; /*  */\n  border: 1px solid var(--border-normal);\n  background: var(--chip-bg);\n  color: var(--text-normal);\n  cursor: pointer; /*  */\n  font-size: 12px; /*  */\n  font-weight: 700; /*  */\n  box-shadow: var(--settings-shadow-inset), var(--settings-shadow-minibtn);\n  transition:\n    background 0.3s ease,\n    border-color 0.3s ease,\n    color 0.3s ease,\n    transform 0.2s ease,\n    box-shadow 0.2s ease;\n}\n.mini-btn:hover {\n  transform: translateY(-1px);\n  box-shadow: var(--settings-shadow-inset), var(--settings-shadow-minibtn-hover);\n} /*  */\n.mini-btn.primary {\n  background: var(--settings-bg-btn-primary);\n  border-color: var(--settings-border-btn-primary);\n  color: var(--settings-text-primary-btn);\n} /*  */\n.mini-btn.subtle {\n  background: var(--settings-bg-btn-subtle);\n  border-color: var(--settings-border-input);\n} /*  */\n\n/*  */\n.vars {\n  display: flex;\n  flex-direction: column;\n  gap: 8px;\n} /*  */\n\n.var-row {\n  /*  */\n  display: grid; /*  */\n  grid-template-columns: 1fr minmax(120px, 1.2fr) auto; /*  /  /  */\n  align-items: center; /*  */\n  gap: 10px; /*  */\n  padding: 8px 10px; /*  */\n  border: 1px solid var(--border-soft);\n  border-radius: 10px; /*  */\n  background: var(--settings-bg-var-row);\n  transition:\n    background 0.3s ease,\n    border-color 0.3s ease;\n}\n.var-key {\n  font-family: ui-monospace, SFMono-Regular, Menlo, Consolas, monospace;\n  font-size: 12px;\n  color: var(--text-subtitle);\n  overflow: hidden;\n  text-overflow: ellipsis;\n  white-space: nowrap;\n  transition: color 0.3s ease;\n} /*  */\n.var-editor {\n  display: flex;\n  align-items: center;\n  gap: 8px;\n} /*  */\n\n.ipt {\n  /*  */\n  width: 100%; /*  */\n  padding: 8px 10px; /*  */\n  border-radius: 8px; /*  */\n  border: 1px solid var(--settings-border-input);\n  background: var(--bg-solid);\n  font-size: 12px; /*  */\n  color: var(--settings-